import BaseApiService from "services/BaseApiService";

const DEFAULT_PATH = "/custom/api/login";
class InvestmentInfoService extends BaseApiService {
  getInvestmentInformation(start = 0, limit = 10, loanUid: string, pageUid: string) {
    const url = `${DEFAULT_PATH}/${loanUid}/${pageUid}/investmentinformation`;

    return this.makeGetRequestWithAuth(url);
  }

  saveInvestmentInformation(payload: any, subStage: string) {
    const url = `${DEFAULT_PATH}/${subStage}/investmentinformation`;

    return this.makePostRequestWithAuth(url, payload);
  }

  updateInvestmentInformation(payload: any, subStage: string) {
    const url = `${DEFAULT_PATH}/${subStage}/investmentinformation`;

    return this.makePutRequestWithAuth(url, payload);
  }

  getCorpHistoricalFinancialInfo(start = 0, limit = 10, loanUid: string, pageUid: string) {
    const url = `${DEFAULT_PATH}/${loanUid}/${pageUid}/corporate/financial/detail`;

    return this.makeGetRequestWithAuth(url);
  }

  updateCorpHistoricalFinancialInfo(payload: any, subStage: string) {
    const url = `${DEFAULT_PATH}/${subStage}/corporate/financial/detail`;

    return this.makePutRequestWithAuth(url, payload);
  }

  getCorpInvestmentInfo(start = 0, limit = 10, loanUid: string, pageUid: string) {
    const url = `${DEFAULT_PATH}/${loanUid}/${pageUid}/corporate/investmentinformation`;

    return this.makeGetRequestWithAuth(url);
  }

  updateCorpInvestmentInfo(payload: any, subStage: string) {
    const url = `${DEFAULT_PATH}/${subStage}/corporate/investmentinformation`;

    return this.makePutRequestWithAuth(url, payload);
  }
}

export default new InvestmentInfoService();
