import BaseApiService from "../../BaseApiService";

// ---------------- Default path ------------------ //
const DEFAULT_PATH = "/custom/api/login";
const DEFAULT_PATH_CORPORATE_OTHER_DETAIL = "/custom/api";

class OtherDetailsService extends BaseApiService {
  getDetails = (applicantId: string) => {
    const endPoint = DEFAULT_PATH + `/other/detail/${applicantId}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  create = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/dde/other/detail`;
    return this.makePostRequestWithAuth(endPoint, payload);
  };

  update = (payload: any) => {
    const endPoint = DEFAULT_PATH + "/dde/other/detail";
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  // ------------------------- To get other details for DDE corporate ------------------------- //

  getOrgOtherDetails = (id: string) => {
    const endPoint = DEFAULT_PATH_CORPORATE_OTHER_DETAIL + `/applicant/uid/${id}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // ------------------------- To save other details for DDE corporate ------------------------- //

  saveOrgOtherDetails = (payload: any) => {
    const endPoint = DEFAULT_PATH + `/dde/other/detail/corporate`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };
}

export default new OtherDetailsService();
